import Vec3 from "../Math/Vec3";
import Mat3 from "../Math/Mat3";
import { Maybe } from "monet";
import GizmoManagerBuilder from "./GizmoManagerBuilder.js";
import CameraBuilder from "./CameraBuilder";
import PositionalLightBuilder from "./PositionalLightBuilder";
import ReferentialBuilder from "./ReferentialBuilder";
import GroundBuilder from "./GroundBuilder";
import TooltipBuilder from "./TooltipBuilder";
import DirectionalLightBuilder from "./DirectionalLightBuilder";
import earcut from "earcut";
import {
  Vector3,
  Color3,
  Quaternion,
  StandardMaterial,
  VertexData,
  Mesh,
  Matrix,
  MeshBuilder,
  VertexBuffer,
  Curve3,
  ActionManager,
  ExecuteCodeAction,
  ShaderMaterial,
  Camera
} from "@babylonjs/core";
import GlobalRef from "../NodeItem/GlobalRef";
import Box from "../NodeItem/Box";
import MaterialCache from "../Utils/MaterialCache";

class Util3d {
  /**
   *
   * @param {*} mesh
   * @param {*} localPosition: Vector3
   * @returns Vec3
   */
  static getGlobalCoordinates(mesh, localPosition) {
    if (
      (mesh.parent && mesh.parent.name === GlobalRef.NAME) ||
      mesh.name === GlobalRef.NAME
    ) {
      return Vec3.ofBabylon(localPosition);
    }
    const meshParent = mesh.parent;
    const meshParentPos = Vec3.ofBabylon(meshParent.position);
    const meshParentRotMat = Util3d.getRotationMatrix(meshParent);
    const meshParentScaling = Vec3.ofBabylon(meshParent.scaling);
    return meshParentRotMat
      .prodVec(
        meshParentScaling.mul(
          Util3d.getGlobalCoordinates(mesh.parent, localPosition)
        )
      )
      .add(meshParentPos);
  }

  /**
   *
   * @param {*} mesh
   * @param {*} localPosition : Vec3
   * @returns
   */
  static getParentCoord(mesh, localPosition, withTranslation = true) {
    const meshParent = mesh.parent;
    const meshParentPos = withTranslation
      ? Vec3.ofBabylon(meshParent.position)
      : Vec3.ZERO;
    const meshParentRotMat = Util3d.getRotationMatrix(meshParent);
    const meshParentScaling = Vec3.ofBabylon(meshParent.scaling);
    return meshParentRotMat
      .prodVec(meshParentScaling.mul(localPosition))
      .add(meshParentPos);
  }

  /**
   *
   * @param {*} mesh
   * @param {*} x: Vector3 in mesh local coord
   * @returns Vector3
   */
  static getGlobalCoord(mesh, x) {
    if (
      (mesh.parent && mesh.parent.name === GlobalRef.NAME) ||
      mesh.name === GlobalRef.NAME
    ) {
      return x;
    }
    const meshParent = mesh.parent;
    const meshParentPos = Vec3.ofBabylon(meshParent.position);
    const meshParentRotMat = Util3d.getRotationMatrix(meshParent);
    const meshParentScaling = Vec3.ofBabylon(meshParent.scaling);
    const childPos = meshParentRotMat
      .prodVec(meshParentScaling.mul(Vec3.ofBabylon(x)))
      .add(meshParentPos);
    return Util3d.getGlobalCoord(meshParent, childPos.toBabylon());
  }

  static getBabylonCoordinates(positionInWorld) {
    return GlobalRef.forwardCoordinates(positionInWorld);
  }

  /**
   *
   * @param {*} mesh
   * @param {*} worldPosition: Vector3 from GlobalRef coordinate frame
   */
  static getLocalCoordFromGlobal(mesh, worldPosition, withTranslation = true) {
    if (
      (mesh.parent && mesh.parent.name === GlobalRef.NAME) ||
      mesh.name === GlobalRef.NAME
    )
      return Vec3.ofBabylon(worldPosition);
    const meshParent = mesh.parent;
    const meshParentPos = withTranslation
      ? Vec3.ofBabylon(meshParent.position)
      : Vec3.ZERO;
    const meshParentRotMat = Util3d.getRotationMatrix(meshParent);
    const meshParentScaling = Vec3.ofBabylon(meshParent.scaling);
    return meshParentScaling
      .map(z => 1 / z)
      .mul(
        meshParentRotMat.dotVec(
          Util3d.getLocalCoordFromGlobal(
            meshParent,
            worldPosition,
            withTranslation
          ).sub(meshParentPos)
        )
      );
  }

  /**
   *
   * @param {*} mesh
   * @param {*} worldPosition: Vec3 from World(Babylon) coordinate frame
   *
   * @returns localPosition: Vec3
   */
  static getLocalCoordFromWorld(mesh, worldPosition, withTranslation = true) {
    /// WARNING: very similar with getLocalCoordFromGlobal
    if (!mesh.parent) {
      return worldPosition;
    }
    const meshParent = mesh.parent;
    const meshParentPos = withTranslation
      ? Vec3.ofBabylon(meshParent.position)
      : Vec3.ZERO;
    const meshParentRotMat = Util3d.getRotationMatrix(meshParent);
    const meshParentScaling = Vec3.ofBabylon(meshParent.scaling);
    // assume scaling != 0
    const inverseScaling = meshParentScaling.map(x => 1.0 / x);

    return inverseScaling.mul(
      meshParentRotMat.dotVec(
        Util3d.getLocalCoordFromWorld(
          meshParent,
          worldPosition,
          withTranslation
        ).sub(meshParentPos)
      )
    );
  }

  /**
   * World to Global coords
   * @param {*} parentView: MainView
   */
  static toGlobalCoord = parentView => {
    const rootMesh = parentView.getRootNode().item.mesh;
    return arrayOfVector3 => {
      const transform = p =>
        Util3d.getLocalCoordFromWorld(
          { parent: rootMesh },
          Vec3.ofBabylon(p)
        ).toBabylon();
      if (Array.isArray(arrayOfVector3)) {
        return arrayOfVector3.map(transform);
      }
      return transform(arrayOfVector3);
    };
  };

  static getRotationMatrix(mesh) {
    const localRotationMatrix = new Matrix();
    const maybeQuaternion = Maybe.fromNull(mesh?.rotationQuaternion);
    maybeQuaternion.forEach(quaternion =>
      quaternion.toRotationMatrix(localRotationMatrix)
    );
    maybeQuaternion.orElseRun(() => {
      const quaternion = Quaternion.RotationYawPitchRoll(
        mesh.rotation.y,
        mesh.rotation.x,
        mesh.rotation.z
      );
      quaternion.toRotationMatrix(localRotationMatrix);
    });
    return Mat3.ofBabylonMatrix(localRotationMatrix);
  }

  static showNormals(scene, mesh, size = 1, color = Color3.Red()) {
    const normals = mesh.getVerticesData(VertexBuffer.NormalKind);
    const positions = mesh.getVerticesData(VertexBuffer.PositionKind);
    const lines = [];
    for (let i = 0; i < normals.length; i += 3) {
      const v1 = Vector3.FromArray(positions, i);
      const v2 = v1.add(Vector3.FromArray(normals, i).scaleInPlace(size));
      lines.push([v1.add(mesh.position), v2.add(mesh.position)]);
    }
    const normalLines = MeshBuilder.CreateLineSystem(
      "normalLines",
      { lines: lines },
      scene
    );
    normalLines.color = color;
    normalLines.parent = mesh;
    return normalLines;
  }

  static orthogonalBasisFromVector(u) {
    const identityMatrix = [
      new Vector3(1, 0, 0),
      new Vector3(0, 1, 0),
      new Vector3(0, 0, 1)
    ];
    const uArray = [u.x, u.y, u.z];
    // choose pivot
    let pivot = 0;
    for (let i = 0; i < 3; i++) {
      if (uArray[i] !== 0) {
        pivot = i;
        break;
      }
    }
    let v = identityMatrix[(pivot + 1) % 3].add(
      identityMatrix[pivot].scale(-uArray[(pivot + 1) % 3] / uArray[pivot])
    );
    v = v.normalize();
    let w = identityMatrix[(pivot + 2) % 3].add(
      identityMatrix[pivot].scale(-uArray[(pivot + 2) % 3] / uArray[pivot])
    );
    w = w.normalize();
    w = w.subtract(v.scale(Vector3.Dot(v, w)));
    return { u: Vector3.FromArray(uArray).normalize(), v: v, w: w.normalize() };
  }

  static createOrientedCone = (
    scene,
    u = new Vector3(1, 0, 0),
    color = Color3.Gray(),
    name = `OrientedCone${randomDigits()}`,
    isPickable = true,
    segments = 16
  ) => {
    const surface = {
      positions: [],
      faces: []
    };
    const d = u.length() / 3;
    const mat3 = Util3d.orthogonalBasisFromVector(u);
    const v = mat3.v;
    const w = mat3.w;

    const orientation = Math.sign(Vector3.Dot(Vector3.Cross(v, w), u));
    // positions
    for (let i = 0; i < segments; i++) {
      const theta = orientation * ((2 * Math.PI) / segments) * i;
      surface.positions.push(
        v.scale(d * Math.cos(theta)).add(w.scale(d * Math.sin(theta)))
      );
    }

    surface.positions.push(Vector3.Zero());
    surface.positions.push(u);

    const zeroIndex = surface.positions.length - 1;
    const uIndex = surface.positions.length - 2;

    for (let i = 0; i < segments; i++) {
      // bottom faces
      surface.faces.push([zeroIndex, (i + 1) % segments, i]);
      // cone faces
      surface.faces.push([uIndex, i, (i + 1) % segments]);
    }

    // mesh
    const mesh = Util3d.meshFromPositionAndFaces(
      name,
      scene,
      surface.positions,
      surface.faces
    );
    mesh.convertToFlatShadedMesh();

    const materialName = `OrientedConeMaterial${name}`;
    mesh.material = MATERIAL_CACHE.get(materialName, scene).orLazy(() => {
      const material = Util3d.getMaterialFromColor(color, scene, materialName);
      MATERIAL_CACHE.put(materialName, scene, material);
      return material;
    });
    mesh.isPickable = isPickable;
    return mesh;
  };

  static createSphere = (
    scene,
    color = Color3.Gray(),
    diameter = 1,
    name = `Sphere${randomDigits()}`,
    isPickable = true
  ) => {
    const sphere = Mesh.CreateSphere(name, 16, diameter, scene);
    const material = Util3d.getMaterialFromColor(
      color,
      scene,
      `SphereMaterial${name}`
    );
    sphere.material = material;
    sphere.isPickable = isPickable;
    return sphere;
  };

  static createBox = (
    scene,
    color = Color3.Gray(),
    size = Box.DEFAULT_SIZE,
    name = `Box${randomDigits()}`,
    isPickable = true
  ) => {
    const box = Mesh.CreateBox(name, size, scene);
    const material = new StandardMaterial(`BoxMaterial${name}`, scene);
    material.diffuseColor = color;
    material.emissiveColor = color;
    box.material = material;
    box.isPickable = isPickable;
    return box;
  };

  static createTubeFromPoints = (
    scene,
    points,
    color = Color3.Gray(),
    radius = 1,
    name = `Tube${randomDigits()}`,
    isPickable = true
  ) => {
    const mesh = MeshBuilder.CreateTube(
      name,
      {
        path: points,
        radius: radius,
        sideOrientation: Mesh.DOUBLESIDE,
        updatable: true,
        cap: Mesh.CAP_ALL
      },
      scene
    );
    const materialName = `Material${name}`;
    mesh.material = MATERIAL_CACHE.get(materialName, scene).orLazy(() => {
      const material = Util3d.getMaterialFromColor(color, scene, materialName);
      MATERIAL_CACHE.put(materialName, scene, material);
      return material;
    });
    mesh.isPickable = isPickable;
    return mesh;
  };

  static positionalLightBuilder = scene => {
    return new PositionalLightBuilder(scene);
  };

  static directionalLightBuilder = scene => {
    return new DirectionalLightBuilder(scene);
  };

  static cameraBuilder(scene) {
    return new CameraBuilder(scene);
  }

  static gizmoManagerBuilder(scene) {
    return new GizmoManagerBuilder(scene);
  }

  static referentialBuilder = scene => {
    return new ReferentialBuilder(scene);
  };

  /**
   * Return a maybe position of intersection of the mouse and a ground mesh
   *
   * @param {Scene} scene Babylon scene
   * @param {Ground} ground a Babylon mesh that represents the ground
   * @returns {Maybe} a maybe Vector3 representing the mouse ground intersection
   */
  static getGroundPosition = function (scene, ground) {
    // Use a predicate to get position on the ground
    const pickInfo = scene.pick(
      scene.pointerX,
      scene.pointerY,
      mesh => mesh === ground
    );
    if (pickInfo.hit) return Maybe.some(pickInfo.pickedPoint);
    return Maybe.none();
  };

  static pickMesh = (scene, ground) => {
    const pickInfo = scene.pick(
      scene.pointerX,
      scene.pointerY,
      mesh => mesh !== ground && mesh.isEnabled()
    );
    if (pickInfo.hit && pickInfo.pickedMesh.isPickable) {
      return Maybe.some(pickInfo.pickedMesh);
    }
    return Maybe.none();
  };

  static groundBuilder = scene => {
    return new GroundBuilder(scene);
  };

  static meshFromPositionAndFaces(
    name,
    scene,
    positions,
    faces,
    updatable = true
  ) {
    const vertexData = new VertexData();

    vertexData.positions = positions.flatMap(z => [z.x, z.y, z.z]);
    vertexData.indices = faces.flatMap(z => z);

    let mesh = new Mesh(name, scene);
    vertexData.applyToMesh(mesh, updatable);
    // mesh.createNormals();
    return mesh;
  }

  /**
   *
   * @param {*} polygon: Array of babylonjs vector3, representing a boundary of a polygon
   */
  static triangulatePolygon(polygon) {
    const triangulation = earcut(
      polygon.map(x => [x.x, x.y, x.z]).flatMap(x => x),
      null,
      3
    );
    const len = triangulation.length / 3;
    const ans = [];
    for (let i = 0; i < len; i++) {
      const i3 = 3 * i;
      ans.push([
        triangulation[i3],
        triangulation[i3 + 1],
        triangulation[i3 + 2]
      ]);
    }
    return ans;
  }

  /**
   *
   * @param {*} polygon: Array of babylonjs vector3, representing a boundary of a polygon
   * Returns a real number representing the orientation of the curve, if positive represents a counterclockwise orientation, clockwise otherwise.
   */
  static computeOrientation(polygon) {
    const n = polygon.length;
    const vec3Poly = polygon.map(x => Vec3.ofBabylon(x));
    let orientation = 0;
    vec3Poly.forEach((a, i) => {
      const modi = (i + 1) % n;
      const edge = vec3Poly[modi].sub(vec3Poly[i]);
      orientation += Vec3.of([-a.y, a.x, a.z]).dot(edge);
    });
    return orientation / 2;
  }

  /**
   * @param {*} arrayOfPoints: array of babylon vec3
   * Returns the average
   */
  static pointAverage(arrayOfPoints) {
    if (!arrayOfPoints || arrayOfPoints.length === 0) return Vector3.Zero();
    return arrayOfPoints
      .reduce((e, x) => e.add(x), Vector3.Zero())
      .scale(1 / arrayOfPoints.length);
  }

  static pointAverageVec3(arrayOfPoints) {
    if (!arrayOfPoints || arrayOfPoints.length === 0) return Vec3.ZERO;
    return arrayOfPoints
      .reduce((e, x) => e.add(x), Vec3.ZERO)
      .scale(1 / arrayOfPoints.length);
  }

  static randomVector3(a, b) {
    return new Vector3.FromArray(
      [0, 0, 0].map(x => a + (b - a) * Math.random())
    );
  }

  static getMaterialFromColor(
    color,
    scene,
    name = `MaterialFromColor${randomDigits()}`
  ) {
    const material = new StandardMaterial(name, scene);
    material.diffuseColor = color;
    material.emissiveColor = color;
    return material;
  }

  static piecewiseCurveDistance = curve => {
    let distance = 0;
    for (let i = 0; i < curve.length - 1; i++) {
      const v = curve[i + 1].subtract(curve[i]);
      distance += v.length();
    }
    return distance;
  };

  static getCurveOrientations = curve => {
    const orientations = [];
    for (let i = 0; i < curve.length - 1; i++) {
      const v = curve[i + 1].subtract(curve[i]);
      orientations.push(Math.atan2(v.y, v.x));
    }
    orientations.push(orientations[orientations.length - 1]);
    return orientations;
  };

  static getSplineFromCurve = (curve, nbPoints = 7) => {
    const closed = false;
    return {
      points: Curve3.CreateCatmullRomSpline(curve, nbPoints, closed).getPoints()
    };
  };

  static splineObj2redis = splineObj => {
    const orientations = Util3d.getCurveOrientations(splineObj.points);
    return splineObj.points.map((x, i) => {
      return [x.x, x.y, orientations[i]];
    });
  };

  static getShaderMaterial = ({
    name = `CustomShader${randomDigits()}`,
    vertex = VERTEX,
    frag = FRAG,
    onBindObservable = SHADER_BIND,
    preProcessShader = SHADER_PRE
  }) => scene => {
    /**
     * Check this for shader re-usage, if a problem appears
     * https://playground.babylonjs.com/#KJ3BVA#4
     * */
    const shaderMaterial = new ShaderMaterial(
      name,
      scene,
      { vertexSource: vertex, fragmentSource: frag },
      {
        attributes: ["position", "normal", "uv"],
        uniforms: [
          "world",
          "worldView",
          "worldViewProjection",
          "view",
          "projection"
        ]
      }
    );
    preProcessShader(scene, shaderMaterial);
    shaderMaterial.onBindObservable.add(() => {
      onBindObservable(scene, shaderMaterial);
    });
    return shaderMaterial;
  };

  static setOrthoView(camera, scene) {
    camera.mode = Camera.ORTHOGRAPHIC_CAMERA;
    const aspect = scene.getEngine().getAspectRatio(camera);
    const distance = camera.position.length();
    camera.orthoLeft = -distance / 2;
    camera.orthoRight = distance / 2;
    camera.orthoBottom = camera.orthoLeft * aspect;
    camera.orthoTop = camera.orthoRight * aspect;
  }

  static setPerspectiveView(camera, scene) {
    camera.mode = Camera.PERSPECTIVE_CAMERA;
  }

  /**
   *
   * @param {*} scene: Babylonjs Scene
   * @param {*} name: String
   * @param {*} begin: Vector3
   * @param {*} end: Vector3
   */
  static getArrow(scene, name, begin, end, radius = 1) {
    const arrowTube = Util3d.createTubeFromPoints(
      scene,
      [begin, end],
      Color3.Gray(),
      radius,
      name + "tube"
    );
    const arrowCone = Util3d.createOrientedCone(
      scene,
      end
        .subtract(begin)
        .normalize()
        .scale(radius * 7),
      Color3.Gray(),
      name + "cone"
    );
    arrowCone.position = end;
    const arrowMesh = new Mesh(name, scene);
    arrowTube.parent = arrowMesh;
    arrowCone.parent = arrowMesh;
    return arrowMesh;
  }

  /*
   * Show tooltip on mesh on mouse over
   *
   * @param {Mesh} mesh: Mesh to add the tooltip
   * @param {Scene} scene: Scene
   * @param {String} tooltipText: Text to be added to the tooltip
   *
   * @returns {TextBlock} TextBlock instance to allow dynamic changes
   *  on the tooltip content by: textBlock.text = "new text"
   */
  static addTooltipToMesh(mesh, scene, tooltipText = "") {
    return new TooltipBuilder(mesh, scene).text(tooltipText).build();
  }

  static addClickActionToMesh(mesh, scene, callback) {
    // Set mesh action manager
    const actionManager = new ActionManager(scene);
    mesh.actionManager = actionManager;
    // Register actions: OnPickDownTrigger (click action)
    actionManager.registerAction(
      new ExecuteCodeAction(ActionManager.OnPickDownTrigger, function (_) {
        callback();
      })
    );
    // Register actions: OnPointerOverTrigger (hover effect)
    actionManager.registerAction(
      new ExecuteCodeAction(ActionManager.OnPointerOverTrigger, function (_) {
        mesh.material.unfreeze();
        mesh.material.emissiveColor = Color3.Gray();
      })
    );
    // Register actions: OnPointerOutTrigger (clear hover effect)
    actionManager.registerAction(
      new ExecuteCodeAction(ActionManager.OnPointerOutTrigger, function (_) {
        mesh.material.emissiveColor = Color3.White();
        setTimeout(() => mesh.material.freeze(), 10);
      })
    );
  }
}

const randomDigits = () => {
  return Math.floor(Math.random() * 1e3);
};

const VERTEX = `precision highp float;

// Attributes
attribute vec3 position;
attribute vec3 normal;
attribute vec2 uv;

// Uniforms
uniform mat4 worldViewProjection;

// Varying
varying vec3 vPosition;
varying vec3 vNormal;
varying vec2 vUV;

void main(void) {
    vec4 outPosition = worldViewProjection * vec4(position, 1.0);
    gl_Position = outPosition;

    vUV = uv;
    vPosition = position;
    vNormal = normal;
}`;
const FRAG = `
precision highp float;

// Varying
varying vec3 vPosition;
varying vec3 vNormal;
varying vec2 vUV;

// Uniforms
uniform mat4 world;

// Refs
uniform vec3 cameraPosition;
uniform vec3 lightPosition;
uniform float time;

void main(void) {
    vec3 vLightPosition = lightPosition;

    // World values
    vec3 vPositionW = vec3(world * vec4(vPosition, 1.0));
    vec3 vNormalW = normalize(vec3(world * vec4(vNormal, 0.0)));
    vec3 viewDirectionW = normalize(cameraPosition - vPositionW);

    // Light
    vec3 lightVectorW = normalize(vLightPosition - vPositionW);
    vec3 color = vec3(0.5,0.,1.);

    // diffuse
    float ndl = max(0., dot(vNormalW, lightVectorW));

    // Specular
    vec3 angleW = normalize(viewDirectionW + lightVectorW);
    float specComp = max(0., dot(vNormalW, angleW));
    specComp = pow(specComp, max(1., 64.)) * 2.;

    gl_FragColor = vec4(color * ndl + vec3(specComp), 1.);
}
`;

const SHADER_BIND = (() => {
  let time = 0;
  let T = new Date().getTime();
  return (scene, shaderMaterial) => {
    const dt = (new Date().getTime() - T) / 1000;
    shaderMaterial.setFloat("time", time);
    shaderMaterial.setVector3("cameraPosition", scene.activeCamera.position);
    if (scene.lights.length > 0) {
      shaderMaterial.setVector3("lightPosition", scene.lights[0].position);
    }
    time = time + dt;
    T = new Date().getTime();
  };
})();

const SHADER_PRE = (scene, shaderMaterial) => {
  shaderMaterial.backFaceCulling = false;
  shaderMaterial.setFloat("time", 0);
  shaderMaterial.setVector3("cameraPosition", scene.activeCamera.position);
  shaderMaterial.setVector3("lightPosition", new Vector3(0, 20, 10));
};

const MATERIAL_CACHE = MaterialCache.getInstance();

export default Util3d;
